body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/**
 * App
 */
.app {
  /* text-align: center; */
}

/**
 * Banner
 */
.banner {
  padding: 50px 10%;
  min-width: 360px;
  min-height: 200px;
  text-align: center;
  max-width: 1500px;
  margin: auto;
}

.banner>h2 {
  margin: 0 0 30px;
}

.banner>p {
  color: #474747;
}

/**
 * Hero
 */
section.banner.hero {
  padding: 0;
  height: 500px;
  top: -400px;
  background: #50bebe; /*  #6dc9c9; dcedc1, a8e6cf, c3e2df, #043953, 60c6cd, cedcf2, 687892 */ /* cv light/dark: #31849b/215868 */
  z-index: 1000;
  overflow: hidden;

  width: -webkit-fill-available;

  width: -moz-available;

  width: fill-available;
  max-width: 100vw;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-transition: height 0.3s ease;
  transition: height 0.3s ease;
}

.hero-content {
  /* display: flex; */
  /* flex-direction: row; */
  /* align-items: center; */
  /* max-width: 1800px; */
  /* width: inherit; */

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  /* width: 100%; */
  /* height: 100%; */
  width: inherit;
  max-width: 1800px;
  padding: 0 10%;
  /* box-sizing: border-box; */
}

.hero img {
  width: 400px;
  height: 400px;
  border-radius: 100%;
  border: 3px solid #b3ddd9;
  box-shadow: 5px 5px 10px rgba(2, 23, 33, 0.12);
}

.conversation {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  /* justify-content: space-evenly; */
  /* flex-grow: 1; */
  /* height: 100%; */
  /* position: relative; */
  width: 100%;
}

.hero .greeting {
  /* position: absolute; */
  /* margin-left: 10px; */
  /* width: 50%; */
  /* max-width: 33vw; */
  /* margin: 10px; */
  z-index: 10;
}

.hero .action {
  /* position: absolute; */
  /* width: 50%; */
  /* max-width: 33vw; */
  /* margin: 10px; */
  margin-left: auto;
  /* right: 0; */
  /* align-self: flex-end; */
}

@media only screen and (max-width: 850px) {
  section.banner.hero {
    padding: 50px 10%;
    height: 100vh;
    position: relative;
    top: 0;
  }

  .hero img {
    position: relative;
    width: 80vw !important;
    height: 80vw !important;
  }

  .hero-content {
    height: auto !important;
    position: relative !important;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }

  .conversation {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .speech-bubble {
    width: 100% !important;
    max-width: 100% !important;
    box-sizing: border-box;
    margin: 10px 0 !important;
  }
  
  .speech-bubble h1 {
    font-size: 1.5em !important;
  }
}

@media only screen and (min-height: 768px) {
  .hero {
    position: -webkit-sticky;
    position: sticky;
  }

  .hero-content {
    position: fixed;
    top: 0;
  }
}

.speech-bubble {
  background: #fbfeff;
  padding: 0 10px;
  height: 70px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgba(2, 23, 33, 0.12);
  /* box-shadow: 8px 14px 38px rgba(45, 51, 56, 0.08), 1px 3px 8px rgba(45, 51, 56, 0.03); */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;

  max-width: calc(100vw / 2 - 100px);
  margin: 10px;
  width: calc(40vw - 100px);
  position: relative;
}

.speech-bubble h1 {
  font-size: 1em;
}

.speech-bubble p {
  margin: 0;
}

@media only screen and (min-width: 1000px) {
  .speech-bubble h1 {
    font-size: 1.5em;
  }
}

@media only screen and (min-width: 1800px) {
  .speech-bubble {
    max-width: calc(1800px / 2 - 100px);
  }
  
  .hero-content {
    /* max-width: 1800px; */
  }
}

.hero .greeting:after {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 0;
	height: 0;
	border: 20px solid transparent;
	border-right-color: #fff;
	border-left: 0;
	border-top: 0;
	margin-top: 0;
	margin-left: -12px;
}

.hero .action:after {
	content: '';
	position: absolute;
  right: 0;
  bottom: 0;
	width: 0;
	height: 0;
	border: 30px solid transparent;
	border-left-color: #fff;
	border-right: 0;
	border-bottom: 0;
	margin-top: 0;
	margin-right: -12px;
}

/**
 * Paragraph
 */
.paragraph h2 {
  text-align: left;
}

.paragraph p {
  text-align: justify;
}

.paragraph-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

/**
 * Showcase
 */
.showcase {
  background: #f2f6f9;
  /* padding: 50px 20px 80px; */
  padding-bottom: 80px;
}

.showcase h2 {
  /* margin: 30px 0 40px; */
}

.showpieces a {
  color: rgb(43, 43, 43);
  text-decoration: none;
}

.showpiece p {
  color: #474747;
}

.showpieces {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.showpiece {
  position: relative;
  top: 0;
  margin: 20px;
  width: 300px;
  height: 400px;
  padding: 10px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 8px 14px 38px rgba(45, 51, 56, 0.08), 1px 3px 8px rgba(45, 51, 56, 0.03);
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
}

.showpiece:hover {
  top: -10px;
}

.showpiece img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}

@media only screen and (min-width: 1100px) {
  .showpieces {
    /* flex-direction: row;
    flex-wrap: wrap; */
    /* justify-content: center; */
  }
}

/**
 * Footer
 */
.footer {
  /* padding: 80px 20px; */
  /* padding-top: 80px; */
  padding-left: 5%;
  padding-right: 5%;
  min-height: 0;
  padding-top: 80px;
  padding-bottom: 80px;
}

/**
 * Misc
 */
.social-button {
  display: inline-block;
  margin: 10px;
  font-size: 1rem;
  padding: 3px 10px;
  border-radius: 5px;
  border: none;
  background: #b3ddd9;
  text-decoration: none;
  color: #474747;
}

.social-button i {
  margin-right: 6px;
}







@media only screen and (min-width: 768px) {
  .banner:not(.showcase) {
    padding: 50px 20%;
  }
}

/* 20x + 1500 + 20x = 100x => 100x = 2500  */
@media only screen and (min-width: 2500px) {
  .banner {
    padding: 50px calc((100% - 1500px) / 2);
  }
}

/* 20x + 1500 + 20x = 100x => 100x = 2500  */
@media only screen and (min-width: 1800px) {
  .showcase {
    padding: 50px calc((100% - 1500px) / 2);
  }
}

